    html: "<div class='checkbox-wrapper-22'>  <label class='switch' for='checkbox'>    <input type='checkbox' id='checkbox' />    <div class='slider round'></div>  </label></div>",
    css: '\n  .checkbox-wrapper-22 .switch {\n    display: inline-block;\n    height: 34px;\n    position: relative;\n    width: 60px;\n  }\n\n  .checkbox-wrapper-22 .switch input {\n    display:none;\n  }\n\n  .checkbox-wrapper-22 .slider {\n    background-color: #ccc;\n    bottom: 0;\n    cursor: pointer;\n    left: 0;\n    position: absolute;\n    right: 0;\n    top: 0;\n    transition: .4s;\n  }\n\n  .checkbox-wrapper-22 .slider:before {\n    background-color: #fff;\n    bottom: 4px;\n    content: "";\n    height: 26px;\n    left: 4px;\n    position: absolute;\n    transition: .4s;\n    width: 26px;\n  }\n\n  .checkbox-wrapper-22 input:checked + .slider {\n    background-color: #66bb6a;\n  }\n\n  .checkbox-wrapper-22 input:checked + .slider:before {\n    transform: translateX(26px);\n  }\n\n  .checkbox-wrapper-22 .slider.round {\n    border-radius: 34px;\n  }\n\n  .checkbox-wrapper-22 .slider.round:before {\n    border-radius: 50%;\n  }\n\n',
    text: '<div class="checkbox-wrapper-22">\r\n  <label class="switch" for="checkbox">\r\n    <input type="checkbox" id="checkbox" />\r\n    <div class="slider round"></div>\r\n  </label>\r\n</div>\r\n\r\n<style>\r\n  .checkbox-wrapper-22 .switch {\r\n    display: inline-block;\r\n    height: 34px;\r\n    position: relative;\r\n    width: 60px;\r\n  }\r\n\r\n  .checkbox-wrapper-22 .switch input {\r\n    display:none;\r\n  }\r\n\r\n  .checkbox-wrapper-22 .slider {\r\n    background-color: #ccc;\r\n    bottom: 0;\r\n    cursor: pointer;\r\n    left: 0;\r\n    position: absolute;\r\n    right: 0;\r\n    top: 0;\r\n    transition: .4s;\r\n  }\r\n\r\n  .checkbox-wrapper-22 .slider:before {\r\n    background-color: #fff;\r\n    bottom: 4px;\r\n    content: "";\r\n    height: 26px;\r\n    left: 4px;\r\n    position: absolute;\r\n    transition: .4s;\r\n    width: 26px;\r\n  }\r\n\r\n  .checkbox-wrapper-22 input:checked + .slider {\r\n    background-color: #66bb6a;\r\n  }\r\n\r\n  .checkbox-wrapper-22 input:checked + .slider:before {\r\n    transform: translateX(26px);\r\n  }\r\n\r\n  .checkbox-wrapper-22 .slider.round {\r\n    border-radius: 34px;\r\n  }\r\n\r\n  .checkbox-wrapper-22 .slider.round:before {\r\n    border-radius: 50%;\r\n  }\r\n</style>\r\n',
