    html: "<div class='checkbox-wrapper-46'>  <input class='inp-cbx' id='cbx-46' type='checkbox' />  <label class='cbx' for='cbx-46'><span>    <svg width='12px' height='10px' viewbox='0 0 12 10'>      <polyline points='1.5 6 4.5 9 10.5 1'></polyline>    </svg></span><span>Checkbox</span>  </label></div>",
    css: '\n  .checkbox-wrapper-46 input[type="checkbox"] {\n    display: none;\n    visibility: hidden;\n  }\n\n  .checkbox-wrapper-46 .cbx {\n    margin: auto;\n    -webkit-user-select: none;\n    user-select: none;\n    cursor: pointer;\n  }\n  .checkbox-wrapper-46 .cbx span {\n    display: inline-block;\n    vertical-align: middle;\n    transform: translate3d(0, 0, 0);\n  }\n  .checkbox-wrapper-46 .cbx span:first-child {\n    position: relative;\n    width: 18px;\n    height: 18px;\n    border-radius: 3px;\n    transform: scale(1);\n    vertical-align: middle;\n    border: 1px solid #9098A9;\n    transition: all 0.2s ease;\n  }\n  .checkbox-wrapper-46 .cbx span:first-child svg {\n    position: absolute;\n    top: 3px;\n    left: 2px;\n    fill: none;\n    stroke: #FFFFFF;\n    stroke-width: 2;\n    stroke-linecap: round;\n    stroke-linejoin: round;\n    stroke-dasharray: 16px;\n    stroke-dashoffset: 16px;\n    transition: all 0.3s ease;\n    transition-delay: 0.1s;\n    transform: translate3d(0, 0, 0);\n  }\n  .checkbox-wrapper-46 .cbx span:first-child:before {\n    content: "";\n    width: 100%;\n    height: 100%;\n    background: #506EEC;\n    display: block;\n    transform: scale(0);\n    opacity: 1;\n    border-radius: 50%;\n  }\n  .checkbox-wrapper-46 .cbx span:last-child {\n    padding-left: 8px;\n  }\n  .checkbox-wrapper-46 .cbx:hover span:first-child {\n    border-color: #506EEC;\n  }\n\n  .checkbox-wrapper-46 .inp-cbx:checked + .cbx span:first-child {\n    background: #506EEC;\n    border-color: #506EEC;\n    animation: wave-46 0.4s ease;\n  }\n  .checkbox-wrapper-46 .inp-cbx:checked + .cbx span:first-child svg {\n    stroke-dashoffset: 0;\n  }\n  .checkbox-wrapper-46 .inp-cbx:checked + .cbx span:first-child:before {\n    transform: scale(3.5);\n    opacity: 0;\n    transition: all 0.6s ease;\n  }\n\n  @keyframes wave-46 {\n    50% {\n      transform: scale(0.9);\n    }\n  }\n\n',
    text: '<div class="checkbox-wrapper-46">\r\n  <input class="inp-cbx" id="cbx-46" type="checkbox" />\r\n  <label class="cbx" for="cbx-46"><span>\r\n    <svg width="12px" height="10px" viewbox="0 0 12 10">\r\n      <polyline points="1.5 6 4.5 9 10.5 1"></polyline>\r\n    </svg></span><span>Checkbox</span>\r\n  </label>\r\n</div>\r\n\r\n<style>\r\n  .checkbox-wrapper-46 input[type="checkbox"] {\r\n    display: none;\r\n    visibility: hidden;\r\n  }\r\n\r\n  .checkbox-wrapper-46 .cbx {\r\n    margin: auto;\r\n    -webkit-user-select: none;\r\n    user-select: none;\r\n    cursor: pointer;\r\n  }\r\n  .checkbox-wrapper-46 .cbx span {\r\n    display: inline-block;\r\n    vertical-align: middle;\r\n    transform: translate3d(0, 0, 0);\r\n  }\r\n  .checkbox-wrapper-46 .cbx span:first-child {\r\n    position: relative;\r\n    width: 18px;\r\n    height: 18px;\r\n    border-radius: 3px;\r\n    transform: scale(1);\r\n    vertical-align: middle;\r\n    border: 1px solid #9098A9;\r\n    transition: all 0.2s ease;\r\n  }\r\n  .checkbox-wrapper-46 .cbx span:first-child svg {\r\n    position: absolute;\r\n    top: 3px;\r\n    left: 2px;\r\n    fill: none;\r\n    stroke: #FFFFFF;\r\n    stroke-width: 2;\r\n    stroke-linecap: round;\r\n    stroke-linejoin: round;\r\n    stroke-dasharray: 16px;\r\n    stroke-dashoffset: 16px;\r\n    transition: all 0.3s ease;\r\n    transition-delay: 0.1s;\r\n    transform: translate3d(0, 0, 0);\r\n  }\r\n  .checkbox-wrapper-46 .cbx span:first-child:before {\r\n    content: "";\r\n    width: 100%;\r\n    height: 100%;\r\n    background: #506EEC;\r\n    display: block;\r\n    transform: scale(0);\r\n    opacity: 1;\r\n    border-radius: 50%;\r\n  }\r\n  .checkbox-wrapper-46 .cbx span:last-child {\r\n    padding-left: 8px;\r\n  }\r\n  .checkbox-wrapper-46 .cbx:hover span:first-child {\r\n    border-color: #506EEC;\r\n  }\r\n\r\n  .checkbox-wrapper-46 .inp-cbx:checked + .cbx span:first-child {\r\n    background: #506EEC;\r\n    border-color: #506EEC;\r\n    animation: wave-46 0.4s ease;\r\n  }\r\n  .checkbox-wrapper-46 .inp-cbx:checked + .cbx span:first-child svg {\r\n    stroke-dashoffset: 0;\r\n  }\r\n  .checkbox-wrapper-46 .inp-cbx:checked + .cbx span:first-child:before {\r\n    transform: scale(3.5);\r\n    opacity: 0;\r\n    transition: all 0.6s ease;\r\n  }\r\n\r\n  @keyframes wave-46 {\r\n    50% {\r\n      transform: scale(0.9);\r\n    }\r\n  }\r\n</style>\r\n',
